import { Component, OnInit } from '@angular/core';
import { Observable, from } from 'rxjs';
import { BloggersService } from '../bloggers.service';
import { Store } from '@ngrx/store';
import { NgForm } from '@angular/forms';
import { Blogger } from '../blogger';
import * as BloggersActions from '../bloggers.actions';


@Component({
  selector: 'app-bloggers',
  templateUrl: './bloggers.component.html',
  styleUrls: ['./bloggers.component.css']
})
export class BloggersComponent implements OnInit {

  bloggersState:Observable<{ bloggers:Blogger[]}>;

  constructor(private bloggerService:BloggersService,
    private store:Store<{bloggers:{bloggers:Blogger[] } }>) { }

  ngOnInit() {
    this.bloggersState = this.store.select('bloggers');
  }


  addBlogger(form:NgForm){
    this.store.dispatch(new BloggersActions.AddBlogger(
      new Blogger(form.value.name, form.value.email,form.value.about)));
      this.bloggersState.subscribe(
        (blog)=>
        {
          this.bloggerService.saveBlogger(blog.bloggers).subscribe();
        }
      );
      form.reset();
  }

}
