import { Component, OnInit, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { NgForm } from '@angular/forms';
import { BloggersService } from '../bloggers.service';
import { Store } from '@ngrx/store';
import { Blogger } from '../blogger'
import * as BloggersActions from '../bloggers.actions';

@Component({
  selector: 'app-add-bloger',
  templateUrl: './add-bloger.component.html',
  styleUrls: ['./add-bloger.component.css']
})

export class AddBlogerComponent implements OnInit {

  bloggersState:Observable<{bloggers:Blogger[]}>;
  @ViewChild('f') form:NgForm;
  
  constructor(private bloggersService: BloggersService,
    private store:Store<{ bloggers:{bloggers:Blogger[] } }>) { }

  ngOnInit() {
    this.bloggersState=this.store.select('bloggers');
  }

  addBlogger(){
    this.store.dispatch(new BloggersActions.AddBlogger(
      new Blogger(this.form.value.name, this.form.value.email, this.form.value.about)));
    this.bloggersState.subscribe(
      (blog)=>{
        this.bloggersService.saveBlogger(blog.bloggers).subscribe();
      }
    );
    this.form.reset();
  }
  
}